
const host = 'https://leyao.tv/yi/public/index.php?pbid=72&s=/'

var Fly = require("flyio/dist/npm/wx")
var fly = new Fly


// -------------------------------
/** 
** 
*/
function request(url, method, data, header = {}) {
  wx.showLoading({
    title: '', //loading
    mask: true
  })

  return new Promise((resolve, reject) => {

    if (method == "GET") {
      fly.get(host + url)
        .then(function (res) {
          wx.hideLoading()
          console.log(res)
          resolve(res.data)
        })
        .catch(function (error) {
          wx.hideLoading()
          console.log(error)
        })
    }
    else {
      fly.post(host + url, data)
        .then(function (res) {
          wx.hideLoading()
          resolve(res.data)
        })
        .catch(function (error) {
          wx.hideLoading()
          console.log(error);
        })
    }



  })
}

function get(url, data) {
  return request(url, 'GET', data)
}

function post(url, data) {
  return request(url, 'POST', data)
}

