/** 
 ** 
 */


//  
function goPay(id) {
  get("shop/api/do_pay?out_trade_no=" + id).then(res => {
    if (res.code == 0) {
      wx.showToast({
        title: res.msg,
        icon: 'none'
      });
      return false
    }
    // 
    if (res.code == 2) {
      wx.showToast({
        title: '',
        icon: 'success'
      });
      setTimeout(function () {
        wx.switchTab({ url: "../center/main" });
      }, 1000);
      return false
    }
    wx.requestPayment({
      timeStamp: res.pay.timeStamp + "",
      nonceStr: res.pay.nonceStr,
      package: res.pay.package,
      signType: "MD5",
      paySign: res.pay.paySign,
      success(res) {
        wx.switchTab({ url: "../center/main" });
      },
      fail(res) { wx.switchTab({ url: "../center/main" }); }
    })
  })
}

//  
function goReceiving(id) {
  wx.showModal({
    title: "",
    content: "",
    success(res) {
      if (res.confirm) {
        get("shop/api/confirm_get?id=" + id).then(res => {
          if (res.code == 1) {
            wx.showToast({
              title: '',
              icon: 'none'
            });
            wx.reLaunch({ url: "../msg/main?msg=" + "" });
          } else {
            wx.showToast({
              title: '',
              icon: 'none'
            });
          }
        });
      } else if (res.cancel) {
        console.log('')
      }
    }
  })
}


export {
  host,
  get,
  post,
  toLogin,
  login,
  timeChange,
  dateDiff,
  goPay,
  goReceiving
}








